"use client";

import levels from "../../data/modules";
import React from "react";
import { useParams } from "next/navigation";
import BackButton from "../../components/BackButton";
import VideoPlayer from "../../components/VideoPlayer";
import ModuleInfo from "../../components/ModuleInfo";
import ResourcesList from "../../components/ResourcesList";
import ExamButton from "../../components/ExamButton";

const ModulePage = () => {
  const { id } = useParams();
  const moduleId = Number(id);

  console.log("ID del m贸dulo recibido:", moduleId);

  if (isNaN(moduleId)) {
    return <p className="text-center text-red-500">ID del m贸dulo no es v谩lido.</p>;
  }

  const allModules = levels.flatMap((level) => level.modules);
  const module = allModules.find((m) => m.id === moduleId);

  console.log("M贸dulo encontrado:", module);

  if (!module) {
    return <p className="text-center text-red-500">M贸dulo no encontrado</p>;
  }

  const examUrl = `/dashboard/progress/module/${moduleId}/exam`;
  console.log("URL del examen:", examUrl);

  return (
    <div className="p-0 lg:p-8 flex flex-col gap-8 bg-background min-h-screen">
      <BackButton onClick={() => window.history.back()} />

      <div className="grid lg:grid-cols-3 gap-8">
        <div className="lg:col-span-2 flex flex-col gap-4">
          <ModuleInfo title={module.title} subtitle={module.subtitle} description={module.description} />
          <VideoPlayer videoUrl={module.videoUrl} />
        </div>

        <div className="flex flex-col gap-6">
          {/*  Asegurar que el bot贸n siempre se renderiza */}
          <ExamButton isDisabled={false} href={examUrl}>
            Comenzar examen
          </ExamButton>

          {Array.isArray(module.resources) && module.resources.length > 0 ? (
            <ResourcesList resources={module.resources} showAllLink="/resources" />
          ) : (
            <p className="text-neutral-500 text-sm">No hay recursos disponibles.</p>
          )}
        </div>
      </div>
    </div>
  );
};

export default ModulePage;
