"use client";
import { useRouter } from "next/navigation";

interface ExamButtonProps {
  isDisabled: boolean;
  href: string;
  children: React.ReactNode;
}

const ExamButton: React.FC<ExamButtonProps> = ({ isDisabled, href, children }) => {
  const router = useRouter();

  const handleClick = () => {
    if (!isDisabled) {
      router.push(href);
    }
  };

  return (
    <button
      disabled={isDisabled}
      onClick={handleClick}
      className={`p-3 rounded-lg text-white font-semibold ${isDisabled ? "bg-gray-400 cursor-not-allowed" : "bg-primary hover:bg-primary-dark"}`}
    >
      {children}
    </button>
  );
};

export default ExamButton;
